* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  max-width: 980px;
  margin: 0 auto;
}
body #header {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
body #header::after {
  content: "";
  position: absolute;
  top: 65px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #dbd9db;
}
body #header .menu {
  display: flex;
  flex-basis: 60%;
  gap: 15px;
}
body #header .menu .logo {
  display: flex;
  margin-left: 15px;
}
body #header .menu .logo img {
  height: 60px;
  padding-bottom: 5px;
}
body #header .menu .menu_element {
  position: relative;
  align-self: center;
  text-decoration: none;
  color: #363135;
  transition: 0.2s linear;
}
body #header .menu .menu_element::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
}
body #header .menu .menu_element:hover::before {
  animation: menu_animate 0.2s ease-in-out both;
  background-color: #4281f0;
}
body #header .menu .menu_element:hover {
  color: #4281f0;
}
body #header .left {
  flex-basis: 35%;
  display: flex;
  justify-content: space-between;
}
body #header .left #language_selector {
  align-self: center;
  background-color: transparent;
  padding: 3px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #dbd9db;
  width: 50%;
  cursor: pointer;
}
body #header .left a {
  position: relative;
  align-self: center;
  text-decoration: none;
  color: #363135;
  transition: 0.2s linear;
  margin-right: 25px;
}
body #header .left a::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
}
body #header .left a:hover::before {
  animation: menu_animate 0.2s ease-in-out both;
  background-color: #4281f0;
}
body #header .left a:hover {
  color: #4281f0;
}
@keyframes menu_animate {
  100% {
    width: 100%;
  }
}
body .petitions_container {
  display: grid;
  grid-template-columns: 85% 15%;
  gap: 60px 0;
}
body .petitions_container .petition {
  grid-column: 1/2;
  width: 90%;
  height: 250px;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow-y: hidden;
  padding: 0 10px;
}
body .petitions_container .petition a {
  text-decoration: none;
  cursor: pointer;
}
body .petitions_container .petition a .title {
  color: #737273;
  padding: 10px 0 5px;
  transition: 0.2s ease-in-out;
}
body .petitions_container .petition a .title:hover {
  color: #4281f0;
}
body .petitions_container .petition .petition_content {
  width: 70%;
  height: 40%;
  margin: 5px 0;
  overflow: hidden;
}
body .petitions_container .petition #petition_data {
  margin-top: 50px;
}
body .petitions_container .petition #petition_data .petition_author,
body .petitions_container .petition #petition_data .petition_date {
  font-size: 12.8px;
}
body .petitions_container .topics_container {
  grid-column: 2/3;
  grid-row: 1/-1;
  position: sticky;
  top: 50px;
  bottom: 0;
}
body .petitions_container .topics_container .topics {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
body .petitions_container .topics_container .topics span {
  text-transform: capitalize;
  border: 1px solid;
  border-color: #ccc;
  border-radius: 15px;
  cursor: pointer;
  color: #737273;
  padding: 5px 10px;
  text-align: center;
  transition: 0.2s ease-in-out;
}
body .petitions_container .topics_container .topics span:hover {
  border-color: #4281f0;
  color: #4281f0;
}
body .petitions_container #load-more-btn {
  grid-column: 1;
  width: 90%;
  background-color: #fff;
  border: 1px solid;
  border-color: #ccc;
  padding: 8px 12px;
  text-transform: uppercase;
  border-radius: 5px;
  color: #363135;
  font-weight: 700;
  margin-bottom: 50px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
body .petitions_container #load-more-btn:hover {
  color: #4281f0;
  border-color: #4281f0;
}
body #footer {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  background-color: #f6f4f6;
  padding: 0 25px 25px;
}
body #footer .links-column .links-column-ul {
  list-style: none;
}
body #footer .links-column .links-column-ul .links-column-title {
  text-transform: uppercase;
  color: #737273;
  font-weight: 700;
  letter-spacing: 1.8px;
  line-height: 1.1;
  font-size: 12.8px;
  margin-bottom: 10px;
}
body #footer .links-column .links-column-ul li {
  padding: 5px 0;
}
body #footer .links-column .links-column-ul li a {
  position: relative;
  text-decoration: none;
  color: #363135;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
body #footer .links-column .links-column-ul li a::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
}
body #footer .links-column .links-column-ul li a:hover::before {
  animation: menu_animate 0.2s ease-in-out both;
  background-color: #4281f0;
}
body #footer .links-column .links-column-ul li a:hover {
  color: #4281f0;
}
body #copyright-section {
  display: flex;
  position: relative;
  background-color: #f6f4f6;
  padding: 15px 0 25px;
}
body #copyright-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 2%;
  width: 96%;
  height: 1px;
  background-color: #dbd9db;
}
body #copyright-section .copy {
  font-weight: 700;
  color: #737273;
  margin-left: 15px;
  font-size: 12.8px;
}